import React from 'react';
import ReactDOM from 'react-dom';
import './index.css';

const antaList = {
  0 : {A : 'あんた！', B : 'よ！あんた！！'},
  1 : {A : 'あんとほんと...', B : 'ってあんた！'},
  2 : {A : 'あんたあんたまじでさぁ', B : 'ってあんたさぁ！'},
  3 : {A : 'あんた', B : 'あんた'},
  4 : {A : 'あんたたち...', B : 'はやめときなさいよあんた'},
  5 : {A : 'あんたオタクほんと', B : 'なんだからあんた！'},
  6 : {A : 'あんた！！！！！！！！', B : 'ってちょっとあんた！！！！！！'},
  7 : {A : 'あんた！？', B : 'ってちょっとあんた！？！！？！'},
  8 : {A : 'ほんとあんたさぁ...' , B : 'はないわよあんた...'}
}

class InputForm extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      value: '',
      antaText: ''};

    this.handleChange = this.handleChange.bind(this);
    this.handleSubmit = this.handleSubmit.bind(this);
  }

  handleChange(event) {
    this.setState({value: event.target.value});
  }

  handleSubmit(event) {

    const min = 0;
    const max = 8;
    const num = Math.floor( Math.random() * (max + 1 - min) ) + min ;

    this.setState({
      antaText : antaList[num].A + this.state.value + antaList[num].B,
    });

    event.preventDefault();
  }


  render() {
    return (
      <div style = {{textAlign: 'center'}}>
        <h1>あんたコンバータ</h1>
        <div>
          子花井しぃすこ（※テスト稼働）
        </div>

        <form onSubmit={this.handleSubmit}>
          <label>
            あんたなに！？　＞＞　 
            <input type="text" value={this.state.value} onChange={this.handleChange} />
          </label>
          <input type="submit" value="あんた変換" />
        </form>

        <h3>
        {this.state.antaText != '' && (
          <p>？？？「{this.state.antaText}」</p>
        )}
        </h3>

      </div>
    );
  }
}

// ========================================

ReactDOM.render(
  <InputForm />,
  document.getElementById('root')
);


